import uCharts from '../../components/u-charts/u-charts.js'
var _self;
var canvaLineA = null;
export default {
	data() {
		return {
			time_range: ['08:25', '09:45', '10:22', '12:12', '13:25', '16:25', '18:25'],
			dateList: [{
					date: '2020年2月19日',
					time: ['09:55:16', '10:25:26', '09:55:16']
				},
				{
					date: '2020年2月20日',
					time: ['09:55:16', '10:25:26', '09:55:16', '09:55:16']
				},
			],
			bpm: '86BPM',

			cWidth: '',
			cHeight: '',
			pixelRatio: 1,
			socketTask: null,
			is_open_socket: false
		};
	},
	onLoad() {
		_self = this;
		_self.cWidth = uni.upx2px(750);
		// console.log(_self.cWidth)
		_self.cHeight = uni.upx2px(200);
		_self.getServerData();
		_self.getSocketData()
	},
	methods: {
		//创建websocket链接
		getSocketData() {
			// 创建一个this.socketTask对象【发送、接收、关闭socket都由这个对象操作】
			this.socketTask = uni.connectSocket({
				// url: 'ws://eztbs.oicp.net:9001',
				url: 'ws://121.40.165.18:8800',
				success: res => {
					console.log('连接成功', res)
				}
			})
			// 消息的发送和接收必须在正常连接打开中,才能发送或接收【否则会失败】
			this.socketTask.onOpen(res => {
				console.log("WebSocket连接正常打开中...！")
				this.is_open_socket = true;
				// 只有连接正常打开中 ，才能正常成功发送消息
				this.socketTask.send({
					data: "uni-app发送一条消息",
					async success() {
						console.log("消息发送成功");
					},
				});
				// 只有连接正常打开中 ，才能正常收到消息
				this.socketTask.onMessage((res) => {
					console.log("收到服务器内容：" + res.data);
				});
			})

			// 监听WebSocket错误
			this.socketTask.onSocketError((res) => {
				console.log('WebSocket连接打开失败，请检查！');
			});
		},

		getServerData() {
			uni.request({
				url: 'https://www.ucharts.cn/data.json',
				data: {},
				success: function(res) {
					// console.log(res.data.data)
					let LineA = {
						categories: [],
						series: []
					};
					//这里后台返回的是数组，所以用等于，如果您后台返回的是单条数据，需要push进去
					LineA.categories = res.data.data.LineA.categories;
					LineA.categories.push('2018', '2019', '2020', '2021', '2022', '2023', '2024', '2025', '2026')
					// LineA.series = res.data.data.LineA.series;
					// 只筛选了最后一条数据进行可视化测试
					LineA.series = res.data.data.LineA.series;
					LineA.series = [LineA.series.pop()];
					// console.log(LineA.series)
					LineA.series[0].data[0] = 15
					LineA.series[0].data[1] = 15
					LineA.series[0].data[2] = 25
					LineA.series[0].data[3] = 7
					LineA.series[0].data[4] = 15
					LineA.series[0].data[5] = 15
					LineA.series[0].data.push(15, 30, 15, 15, 25, 5, 15, 15, 25, 5, 15)
					_self.showLineA("myChart", LineA);
				},
				fail: () => {
					_self.tips = "网络错误，小程序端请检查合法域名";
				},
			});
		},
		showLineA(canvasId, chartData) {
			// 图表实例和配置
			canvaLineA = new uCharts({
				$this: _self,
				canvasId: canvasId,
				colors: ['#FFFFFF'],
				type: 'line',
				fontSize: 12,
				dataLabel: false,
				dataPointShape: false,
				background: '#24C789',
				pixelRatio: _self.pixelRatio,
				categories: chartData.categories,
				series: chartData.series,
				animation: true,
				legend: {
					show: false
				},
				xAxis: {
					gridColor: '#CCC',
					disabled: true,
					axisLine: false,
					fontColor: '#FFF',
					splitNumber: 15,
				},
				yAxis: {
					data: [{
						position: 'right',
						fontColor: '#FFF',
						axisLineColor: '#24C789',
						min: 0,
						max: 30,
						disabled: true,
						format: (val) => {
							return val.toFixed(0)
						}
					}],
					gridColor: '#CCC',
					splitNumber: 3,
				},
				width: _self.cWidth * _self.pixelRatio,
				height: _self.cHeight * _self.pixelRatio,
				extra: {
					line: {
						type: 'straight'
					},
					tooltip: {
						gridType: 'dash',
						dashLength: 5,
						gridColor: '#24C789'
					}
				}
			});

		},
		touchLineA(e) {
			canvaLineA.showToolTip(e, {
				format: function(item, category) {
					return category + ' ' + item.name + ':' + item.data
				}
			});
		}
	},
}
